<template>
  <div class="myFootprint">
    <div class="title1">
      <div class="back" @click="back"></div>
      {{title}}
    </div>
    <div class="tab_section1">
      <div class="tab_content" v-for="(item,index) of recommendArr" :key="index">
        <img @click="onSectionView(item)" :src="item.image" class="left" alt />
        <div class="right">
          <div @click="onSectionView(item)" class="title">
            <span>
              {{item.title}}
            </span>
          </div>
          <p @click="onSectionView(item)">{{item.text}}</p>
          <div class="bottom">
            <img src="../assets/images/icon_visits@2x.png" alt />
            <div class="tel">{{item.tel}}</div>
            <img src="../assets/images/btn_good_normal@2x1.png" alt />
            <div class="tel">{{item.sureCount}}</div>
            <ul class="bo_r" @click="item.identifySub.shadeShow=true">
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
        <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
          <div class="shade" v-show="item.identifySub.shadeShow">
            <div class="shade_con" @click="item.identifySub.shadeShow=false"></div>
            <img
              @click="onCollect(item)"
              class="img1"
              :src="item.identifySub.shadeImg==false?require('../assets/images/btn_collect_normal@2x.png'):require('../assets/images/btn_collect_selected@2x.png')"
              alt
            />
            <img
              @click="onLike(item)"
              class="img2"
              :src="item.identifySub.shadeImg1==false?require('../assets/images/btn_good_normal@2x.png'):require('../assets/images/btn_good_selected@2x.png')"
              alt
            />
            <div class="disabled1" v-show="item.identifySub.shadeImg1"></div>
            <img
              @click="item.identifySub.shadeImg2 = true,item.identifySub.trampleShow = true,item.identifySub.shadeShow = false"
              class="img3"
              :src="item.identifySub.shadeImg2==false?require('../assets/images/btn_bad_normal@2x.png'):require('../assets/images/btn_bad_selected@2x.png')"
              alt
            />
            <div class="disabled" v-show="item.identifySub.shadeImg2"></div>
          </div>
        </transition>
        <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
          <div class="trample" v-show="item.identifySub.trampleShow">
            <p @click="onTreadCause($event,item)">设计不佳</p>
            <p @click="onTreadCause($event,item)">数据不准</p>
            <p @click="onTreadCause($event,item)">不感兴趣</p>
            <p @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item">其他意见</p>
          </div>
        </transition>
      </div>
    </div>
    <van-overlay :show="writeShow" z-index="300">
      <div class="submitInfo" @click.stop>
        <van-field
          v-model="treadMsg"
          rows="6"
          autosize
          type="textarea"
          maxlength="100"
          placeholder="请输入具体意见描述"
          show-word-limit
        />
        <van-button
          type="primary"
          color="linear-gradient(270deg, #45A4B1 0%, #2A7875 100%)"
          block
          @click="onTreadIdea()"
        >提交</van-button>
        <img @click="writeShow = false,treadMsg = '',secConObj.identifySub.shadeImg2 = false" src="../assets/images/btn_close@1x.png" alt />
      </div>
    </van-overlay>
  </div>
</template>
<script>
export default {
  data() {
    return {
      recommendArr:[],
      secConObj: {},
      writeShow: false,
      treadMsg: "",
      userInfoObj: this.$store.state.userInfoObj || {},
      id:this.$route.query.id,
      title:this.$route.query.title,
    };
  },
  mounted(){
    this.$nextTick(()=>{
      this.getRecommendData()
    }) 
  },
  methods: {
    back(){
      this.$router.go(-1)
    },
    //获取数据
    getRecommendData() {
      this.$axios
        .post("/hopeModule/getModuleByShortCutBar", {
          // "aamid": this.userInfoObj.aamid,
          // "deptid": this.userInfoObj.deptid,
          // "odeptid": this.userInfoObj.odeptid,
          aamid: "010203",
          deptid: "0010101032",
          odeptid: "0010100003",
          "shortname":this.title,
          "moduleidArr":this.id

        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.recommendArr = [...res.data.data] || [];
          }
        });
    },
    //section收藏和取消收藏
    onCollect(item) {
      item.identifySub.shadeImg = !item.identifySub.shadeImg;
      if (item.identifySub.shadeImg == false) {
        this.$axios
          .post("/hopeModule/deleteWatchHopeModule", {
            aamid: "010203",
            // "aamid": this.userInfoObj.aamid,
            "moduleid": item.moduleid
          })
          .then(res => {
            if (res.status === 200 && res.data.status === "000000") {
              this.$toast({
                message: "取消收藏",
                icon: require("../assets/images/btn_collect_normal@2x.png")
              });
            }
          });
      } else {
        this.$axios
          .post("/hopeModule/insertWatchHopeModule", {
            aamid: "010203",
            // "aamid": this.userInfoObj.aamid,
            "moduleid": item.moduleid
          })
          .then(res => {
            if (res.status === 200 && res.data.status === "000000") {
              this.$toast({
                message: "已收藏",
                icon: require("../assets/images/btn_collect_selected@2x.png")
              });
            }
          });
      }
    },
    //section点赞
    onLike(item) {
      this.$axios
        .post("/hopeComments/insertHopeComments", {
          aamid: "010203",
          // "aamid": this.userInfoObj.aamid,
          "moduleid": item.moduleid,
          "logtime": Date.parse(new Date()),
          "comments": 1,
          "description": ""
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            item.identifySub.shadeImg1 = true;
            this.getSectionView();
          }
        });
    },
    //section点踩原因
    onTreadCause(e, item) {
      this.$axios
        .post("/hopeComments/insertHopeComments", {
          aamid: "010203",
          // "aamid": this.userInfoObj.aamid,
          "moduleid": item.moduleid,
          "logtime": Date.parse(new Date()),
          "comments": 0,
          "description": e.target.innerHTML
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            item.identifySub.trampleShow = false;
            item.identifySub.shadeImg2 = true
          }
        });
    },
    //section点踩意见
    onTreadIdea() {
      if(this.treadMsg){
        this.$axios
          .post("/hopeComments/insertHopeComments", {
            aamid: "010203",
            // "aamid": this.userInfoObj.aamid,
            "moduleid": this.secConObj.moduleid,
            "logtime": Date.parse(new Date()),
            "comments": 0,
            "description": this.treadMsg
          })
          .then(res => {
            if (res.status === 200 && res.data.status === "000000") {
              
                this.writeShow = false;
                this.secConObj.identifySub.shadeImg2 = true;
                this.treadMsg = "";
              
            }
          });
      }else{
        this.$toast("意见不能为空")
        this.secConObj.identifySub.shadeImg2 = true;
      }
    },
    //点击section跳转增加足迹
    onSectionView(item) {
      this.$axios
        .post("/hopeUserLog_h/insertUserLog_h", {
          aamid: "010203",
          // "aamid": this.userInfoObj.aamid,
          "moduleid": item.moduleid,
          "logtime": Date.parse(new Date())
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            window.location.href = item.url;
          }
        });
    },
    
  }
};
</script>
<style lang="less" scoped>
.myFootprint{
  width: 100%;
  background: #f1f1f1;
  overflow: hidden;
  .submitInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    padding: 1rem 0.2rem;
    box-sizing: border-box;
  }
  .title1 {
    width: 100%;
    height: 0.5rem;
    text-align: center;
    line-height: 0.5rem;
    color: #000;
    font-size: .15rem;
    background: #fff;
    padding: 0 0.5rem;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    .back {
      position: absolute;
      left: 0;
      top: 0;
      width: 0.4rem;
      height: 100%;
      background: url("../assets/images/icon_return@2x.png") no-repeat center
        center;
      background-size: 0.2rem 0.2rem;
    }
  }
}
.tab_section1 {
  width: 100%;
  padding: 0 0.12rem;
  box-sizing: border-box;
  margin-top: .66rem;
  .tab_content {
    width: 100%;
    height: 1.15rem;
    background: #ffffff;
    margin-top: 0.16rem;
    padding: 0.12rem;
    box-sizing: border-box;
    position: relative;
    .shade {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      left: 0;
      top: 0;
      .shade_con {
        width: 100%;
        height: 100%;
      }
      .img1 {
        width: 0.4rem;
        height: 0.4rem;
        position: absolute;
        left: 0.4rem;
        top: 50%;
        margin-top: -0.2rem;
      }
      .img2 {
        width: 0.4rem;
        height: 0.4rem;
        position: absolute;
        left: 1.67rem;
        top: 50%;
        margin-top: -0.2rem;
      }
      .img3 {
        width: 0.4rem;
        height: 0.4rem;
        position: absolute;
        right: 0.4rem;
        top: 50%;
        margin-top: -0.2rem;
      }
      .disabled {
        width: 0.4rem;
        height: 0.4rem;
        position: absolute;
        right: 0.4rem;
        top: 50%;
        margin-top: -0.2rem;
        z-index: 50;
      }
      .disabled1 {
        width: 0.4rem;
        height: 0.4rem;
        position: absolute;
        left: 1.67rem;
        top: 50%;
        margin-top: -0.2rem;
        z-index: 50;
      }
    }
    .trample {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      padding: 0.19rem 0.5rem;
      box-sizing: border-box;
      p {
        width: 1.14rem;
        height: 0.3rem;
        border-radius: 0.15rem;
        border: none;
        background: #fff;
        text-align: center;
        line-height: 0.3rem;
        font-size: 0.13rem;
        color: #232323;
      }
    }
    .left {
      width: 0.95rem;
      height: 100%;
      float: left;
      border-radius: 0.05rem;
    }
    .right {
      width: 2.22rem;
      height: 100%;
      float: left;
      margin-left: 0.1rem;
      .title {
        width: 100%;
        height: 0.35rem;
        line-height: 0.35rem;
        font-size: 0.14rem;
        color: #3a3a3a;
        font-weight: bold;
      }
      p {
        width: 100%;
        height: 0.42rem;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: 0.12rem;
        color: #969696;
      }
      .bottom {
        width: 100%;
        height: 0.18rem;
        img {
          width: 0.18rem;
          height: 100%;
          float: left;
        }
        .tel {
          width: 0.5rem;
          height: 100%;
          line-height: 0.18rem;
          float: left;
          margin-left: 0.02rem;
          font-size: 0.1rem;
          color: #a5a5a5;
        }
        .bo_r {
          width: 0.18rem;
          height: 100%;
          justify-content: space-between;
          display: flex;
          align-items: center;
          padding: 0 0.03rem;
          box-sizing: border-box;
          background: #fff;
          border: none;
          margin: 0;
          float: right;
          li {
            width: 0.02rem;
            height: 0.02rem;
            background: #d8d8d8;
          }
        }
      }
    }
  }
}
</style>